@charset "utf-8";

/* 공통서식 common header, footer */
header{
  line-height: 60px;
  text-align:center;
  background-color: #000;
}
header h1 img{width:140px;}

body{
  max-width:767px;
  min-width:340px;
  width:100%;
}

footer{
  clear:both;
  background:var(--base_color);
  color:#fff;
  text-align:center;
  line-height: 40px;
}
footer > address{font-style:normal;}
footer .top_btn{
  right: 20px;
  bottom: 20px;
  z-index: 9999;
}