@charset "utf-8";

/* ProductList파일 서식
1. ProductInput.js
2. ProductList.js */

/* product input 서식 시작 */
.input_item{
  padding-top:20px;
  overflow: hidden;
}
.input_item > p{}
.input_item > p > label{
  display:block;
  font-weight: bold;
  padding:10px 5px;
}
.input_item > p > input{
  width:100%;
  height:30px;
  border:none;
  background-color: #ccc;
  padding:5px;
}
.input_item input[type=button]{
  background: #1a1b1f;
  color:#fff;
  font-weight: bold;
  font-size: 16px;
  height:60px;
  margin:20px 0px;
  border:none;
  cursor: pointer;
}

.product_list{
  width:100%;
  overflow:hidden;
  display:grid;
  grid-template-columns: repeat(2, 1fr); /* 2개씩 */
  gap: 3%; /* 카드 사이 간격 */
  padding:1%;
}
.product_list > li{}
.product_list > li img{width:100%;}
.product_list > li .name{
  font-size:16px;
  font-weight: bold;
}
.product_list > li .price{
  font-size: 1rem;
  text-align:right;
  font-weight: bold;
  color:#f00;
}
.product_list > li p input{
  width: 20%;
  height:30px;
  background-color: #1a1b1f;
  color:#fff;
  border: none;
  margin: 10px;
}